@import "./App.scss";

.blog-container {
  display: flex;
  flex-direction: column;
  align-items: center;

  .top {
    display: flex;
    flex-direction: row;
    align-items: center;
  }

  #title {
    font-size: 3rem;
    margin: 10px 80px;
  }

  &.in-view {
    opacity: 1;
    transform: scale(1);

    .top {
      animation: fadeInUp 3s ease forwards;
    }

    .description {
      animation: fadeInUp 3s ease forwards;
    }

    .card-container {
      animation: fadeInUp 3s ease forwards;
    }
  }

  .description-container {
    display: grid;
    grid-template-columns: repeat(2, 1fr);

    .description {
      display: flex;
      flex-direction: column;

      p {
        margin: 0px;
      }
    }
  }

  .card-container {
    margin: 10px;
    display: grid;
    grid-template-columns: repeat(4, 0fr);
    gap: 10px;

    .blog-card {
      // width: 100%;
      display: block;
      width: 400px;
      height: 200px;
      margin: 5px;
      border-radius: 30px;
      transition: width 2s ease, height 2s ease, margin-top 1s ease,
        transform 1s ease;
      position: relative;
      z-index: 0;
    }

    .blog-card-title {
      position: absolute;
      color: white;
      width: calc(100% - 20px);
      text-align: left;
      text-shadow: 3px 3px 3px black;
      margin-left: 20px;
      bottom: 10px;
      z-index: 2;
    }

    .images-left .images-right {
      margin: 0px;
    }

    .blog-card-link {
      position: relative;
      display: inline-block;
      text-decoration: none;
    }

    .active {
      width: 400px;
      height: auto;
      margin-top: 20px;
      transform: 2s scale(1);
    }

    .card-hovered {
      margin-top: 20px;
    }
  }

  // responsive tablet
  @media only screen and (max-width: $screen-tablet) {
    .top {
      flex-direction: column;
    }

    #title {
      font-size: 3rem;
      margin: 10px 40px;
    }

    .description-container {
      grid-template-columns: repeat(1, 1fr);

      .description {
        margin: 20px;
        width: 100%;
        justify-content: center;
      }
    }

    .card-container {
      grid-template-columns: repeat(2, 0fr);

      .blog-card {
        // width: 100%;
        display: block;
        width: 300px;
      }
    }
  }

  @media only screen and (max-width: $screen-phone) {
    #title {
      font-size: 2rem;
    }

    .card-container {
      grid-template-columns: repeat(1, 0fr);

      .blog-card {
        // width: 100%;
        display: block;
        width: 300px;
      }
    }

    .navbar {
      display: grid;
      grid-template-columns: repeat(3, 0fr);
    }
  }
}
