@import "./App.scss";

.header {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  margin: 30px;

  .header-description {
    display: flex;
    flex-direction: column;
    width: 50%;

    .header-top {
      display: flex;
      flex-direction: row;
      justify-content: space-between;
      margin: 0px 20px 100px 20px;

      .logo {
        width: 40px;
        height: 40px;
        animation: fadeInRight 3s ease forwards;
      }
    }

    .header-bottom {
      display: flex;
      flex-direction: row;
      justify-content: start;
      margin: 40px;
    }

    .description {
      display: flex;
      flex-direction: column;
      justify-content: center;
      margin: 30px 40px;
      animation: fadeInRight 3s ease forwards;
    }
    .navbar {
      display: flex;
      flex-direction: row;
      margin: 0px 40px;
      animation: fadeInRight 3s ease forwards;
    }
  }

  .container {
    display: flex;
    flex-direction: column;
    width: 50%;

    .header-image-hero {
      background-size: cover;
      background-repeat: no-repeat;
      border-radius: 30px;
      width: 100%;
      height: 550px;
      display: flex;
      justify-content: flex-end;
      align-items: flex-end;
      animation: fadeInLeft 2s ease forwards;

      .arrow-hero {
        margin: 20px;
      }
    }

    .boxes {
      margin: 20px 0px 0px;
      display: flex;
      flex-direction: row;
      justify-content: space-between;
      .pink-box {
        background-color: #f19295;
        border-radius: 20px;
        width: 40%;
        height: 250px;
        display: flex;
        flex-direction: column;
        justify-content: center;
        padding: 0px 20px;
        margin-right: 10px;
        animation: fadeInLeft 2.5s ease forwards;

        .rating {
          border-radius: 50px;
          background-color: #d98486;
          width: 90px;
          padding: 10px 0px;
        }
      }

      .yellow-box {
        background-color: #e0c979;
        border-radius: 20px;
        width: 55%;
        height: 250px;
        animation: fadeInLeft 3s ease forwards;

        .title {
          display: flex;
          flex-direction: row;
          justify-content: space-between;
          align-items: center;
          margin: 20px;
        }
      }
    }
  }

  // responsive tablet
  @media only screen and (max-width: $screen-tablet) {
    flex-direction: column;
    margin: 15px;

    .header-description {
      width: 100%;

      .header-top {
        margin: 0px 10px 50px 10px;
      }
      .navbar {
        margin: 40px;
        justify-content: center;
      }
    }

    .container {
      width: 100%;
    }
  }

  @media only screen and (max-width: $screen-phone) {
    .header-description {
      width: 100%;

      .header-top {
        margin: 0px 5px 25px 5px;
      }

      .header-bottom {
        margin: 10px;
      }
      .navbar {
        margin: 10px;
        justify-content: center;
      }
      .description {
        margin: 10px 15px;
      }
    }

    .container {
      width: 100%;
      align-items: center;

      .header-image-hero {
        width: 100%;
        height: 300px;
      }
      .boxes {
        margin: 20px 0px 0px;
        .pink-box {
          height: 150px;
          .rating {
            width: 90px;
            padding: 5px 0px;
          }
        }

        .yellow-box {
          height: 150px;

          .title {
            margin: 10px;
          }
        }
      }
    }
  }
}
